import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import withRouter from '../../routes/WithRouter';
const AsideMenu = React.lazy(() => import('./AsideMenu'));

class Aside extends Component {
       componentDidMount() {
              setTimeout(() => {
                     var menuToggle = document.getElementById("main_menu_toggle");
                     if (menuToggle) menuToggle.addEventListener("click", this.toggleMenu);
              }, 100)
       }
       toggleMenu = () => {
              var rootEle = document.getElementById('rootDashoard');
              let asideMenu = document.getElementById('aside__toggle')
              if (rootEle) rootEle.classList.toggle('aside-on');
              if (asideMenu && asideMenu.classList.contains('show')) {
                     let button = document.getElementById('toggle_aside')
                     if (button) button.click()
              }
       };
       render() {
              const { authorized, navigate } = this.props
              return <>
                     <aside className="sidebar-menu-main border-end bg-body">
                            <Suspense fallback={<></>}>
                                   <AsideMenu navigate={navigate} authorized={authorized} />
                            </Suspense>
                     </aside>
                     <div className="aside-overlay" onClick={this.toggleMenu}></div>
              </>
       }
}

const mapStateToProps = state => {
       return { authorized: state.authorized }
}

export default connect(mapStateToProps)(withRouter(Aside));  